import React from "react";

//import { PageTitle } from '../../layout-components';
import { IconButton, Box, Card, CardContent } from "@material-ui/core";
import { InputGroup, FormControl } from "react-bootstrap";
import axios from "axios";
import { Row, Col, Tabs, Tab } from "react-bootstrap";
import i18n from './../../../i18n';

import Aux from "../../../hoc/_Aux";
var urlbackend = require("../../../env/env")();

class ReplanificationPickupadmin extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      pendingPackages: [],

      drivers: [],
      driver: "",
    };
  }
  componentDidMount() {
    const config = {
        headers: {
            'Content-Type': 'application/json',
            'Access-Control-Allow-Headers': 'Authorization',
            "x-access-token": localStorage.getItem('token'),
            'Authorization': 'Bearer ' + localStorage.getItem('token'),
        },
    };
    var getdrivers = axios.get(urlbackend + "/driver", config);
    var getreschedulePackage = axios.get(urlbackend + "/pickup/reschedule/list?current_page=1&per_page=500", config);
    axios
    .all([getdrivers, getreschedulePackage])
    .then(
      axios.spread((...response) => {
        this.setState({ pendingPackages: response[1].data.data.data });
        console.log(response[1].data.data.data)

        this.setState({ drivers: response[0].data.data });
      })
    )
    .catch((errors) => {
      console.log(errors)
    })

  // axios.get(urlbackend+"/reschedulePackage/list" , config).then((response)=>{
  //     console.log(response.data)
  //     this.setState({pendingPackages:response.data.content})
  // }).catch(e=>{
  //   console.log(e)
  // })
  }
  savedriver(iddriver, idpacket) {
    //
    console.log(iddriver);
    console.log(idpacket);
    this.setState({ drivers: []});
    const config = {
        headers: {
            'Content-Type': 'application/json',
            'Access-Control-Allow-Headers': 'Authorization',
            "x-access-token": localStorage.getItem('token'),
            'Authorization': 'Bearer ' + localStorage.getItem('token'),
        },
    };

    axios
      .put(
        urlbackend + "/pickup/to_reschedule",
        { driver: iddriver , coliss:[idpacket]},
        config
      )
      .then((response) => {
        alert("add Succefull");
        console.log(response.data);
        this.componentDidMount();
      })
      .catch((e) => {
        console.log(e);
      });
  }
  reloaddata(){
    this.componentDidMount()
  }
  render() {
    return (
      <Aux>

        <Row>
          <Col>
     <button   className="btn btn-info" onClick={()=> this.reloaddata()} >{i18n.t('refresh')}</button>

            <h5>{i18n.t('pickups-anomaly')}</h5>

            <hr />
            <Tabs defaultActiveKey="home">
              <Tab eventKey="home" title={i18n.t('waiting-to-reschedule')}>
                <Card className="card-box mb-4">
                  <div className="card-header">
                    <div className="card-header--title">
                      {/* <b>This table card has custom content</b> */}
                    </div>
                    <Box className="card-header--actions">
                      <IconButton
                        size="small"
                        color="primary"
                        className="text-primary"
                        title="View details"
                      ></IconButton>
                    </Box>
                  </div>
                  <CardContent className="p-0">
                    <div className="table-responsive">
                      <table className="table  table-hover text-nowrap mb-0">
                        <thead className="table thead-light mb-0">
                          <tr>
                            <th>{i18n.t('provider')}</th>
                            <th className="text-center">{i18n.t('client-name')}</th>
                            <th className="text-center">{i18n.t('driver')}</th>
                            <th className="text-center">{i18n.t('creation-date')}</th>
                            <th className="text-center">{i18n.t('action-event')}</th>
                            <th className="text-center"></th>
                          </tr>
                        </thead>
                        <tbody>
                          {this.state.pendingPackages.map((pickup, index) => (
                            <tr key={index}>
                              <td>
                                <div className="d-flex align-items-center">
                                  <div>
                                    <a
                                      href="#/"
                                      onClick={(e) => e.preventDefault()}
                                      className="font-weight-bold text-black"
                                      title="..."
                                    >
                                      {pickup.colis.provider.username} :{" "}
                                      {pickup.colis.provider.phone_number}
                                    </a>
                                    <span className="text-black-50 d-block">
                                      {pickup.colis.provider.code_postal} :{" "}
                                      {pickup.colis.provider.address}
                                    </span>
                                  </div>
                                </div>
                              </td>
                              <td className="text-center">
                                <div className="h-auto  badge ">
                                  {pickup.colis.name_complete}{" : "}
                                  {pickup.colis.phone_number} <br/>
                                  {pickup.colis.address}
                                </div>
                              </td>
                             
                              <td className="text-center">
                                <div className="h-auto  badge ">
                                  {pickup.driver
                                    ? pickup.driver.username
                                    : "non approv√©"}
                                </div>
                              </td>
                              <td className="text-center">
                                <div className="h-auto  badge ">
                                  {pickup.created_at}
                                </div>
                              </td>

                              <InputGroup className="mb-3">
                                <FormControl
                                  as="select"
                                  id="{this.state.driver}"
                                  onChange={(value) =>
                                    this.savedriver(
                                      value.target.value,
                                      pickup.colis.id
                                    )
                                  }
                                  custom
                                >
                                  <option>Choisir un chauffeur</option>

                                  {this.state.drivers.map((driver, index) => (
                                    <option
                                      className=" badge badge-info"
                                      key={driver.id}
                                      value={driver.id}
                                    >
                                      {driver.username}
                                    </option>
                                  ))}
                                </FormControl>
                              </InputGroup>
                              <td>
                                {/* <div className="h-auto  badge badge-danger"> 
                          <button className="badge-info" onClick={()=>this.savedriver()}>savedriver</button>

                    </div> 
                        <div className=" badge badge-danger">
                          delete

                   </div> */}
                              </td>
                              <td className="text-center">
                                <Box>
                                  <IconButton color="primary" size="small">
                                    {/* <FontAwesomeIcon icon={['fas', 'ellipsis-h']} /> */}
                                  </IconButton>
                                </Box>
                              </td>
                            </tr>
                          ))}
                        </tbody>
                      </table>
                    </div>
                  </CardContent>
                </Card>
              </Tab>
           </Tabs>
          </Col>
        </Row>
      </Aux>
    );
  }
}

export default ReplanificationPickupadmin;
